  }
}



.navbar-collapse{
    /* background-color:#282828; */
    padding:15px;
}

.navbar-toggler{
   float:right;
}

nav{
    color:white;
    /* background-color:#282828; */
    margin-bottom:-59px;
    position:absolute;
    z-index:9999;
    border-bottom:1px solid white;
    background-color:rgba(0, 0, 0, .7);
}

.nav-link{
    cursor:pointer;
    display: inline-block;
  position: relative;
  padding-bottom: 3px;
}

.nav-link:after {
    content: '';
    display: block;
    margin: auto;
    height: 3px;
    width: 0px;
    background: transparent;
    transition: width .3s ease, background-color .3s ease;
  }

  .nav-link:hover:after {
    width: 100%;
    background:#00FFA2;
  }

.navbar-brand{
    color:white;
    cursor:pointer;
}

.navbar-brand:hover{
    color:#00FFA2;
    transition:all 300ms;
}

.menu-icons{
    margin:0 10px;
    color:white;
    font-size:22px;
    transition:all 200ms;
}

.menu-icons:hover{
    color:#00FFA2;
    transition:all 200ms;
}

.navbar-brand{
    font-weight:bold;
}

.menu-icons-wrap{
    position:absolute;
    right:10px;
    top:10px;
}

/* The sticky class is added to the header with JS when it reaches its scroll position */


.sticky-menu {
    position: fixed;
    top:10px;
    z-index:99999;
    left:10px;
    padding:13px 20px;
    transition:all 300ms;
    background-color:white;
    box-shadow:0 0 5px black;
    border-radius:5px;
    transition:all 300ms;
  }

  .sticky-menu i{
      color:black;
  }

.sticky {
    position: fixed;
    top: 10px;
    z-index:99999;
    right:10px;
    transition:all 300ms;
  }

  .sticky .menu-icons{
      color:black;
  }

.white-bg{
    position:fixed;
    background-color:white;
    width:180px;
    height:50px;
    right:0;
    top:0;
    /* border-radius:0 0 0 5px; */
    box-shadow:0 0 5px black;
