}



#menu{
    position:fixed;
    top:0;
    height:100vh;
    z-index:9999;
    background-color:rgba(0, 0, 0, .95);
    color:white;
    padding:20px;
    transition:all 300ms;
}

#menu .nav-link{
    text-shadow:2px 2px 0 black;
}

.menu-closed{
    left:-300px;
    width:300px;
}

.menu-open{
    left:0;
    width:300px;
}

#menu .nav-link{
    color:white;
}

#menu .nav-link:hover{
    color:#00FFA2;
    transition:all 300ms;
}

.menu-close{
    float:right;
    cursor: pointer;
}

.menu-close:hover{
    color:#00FFA2;
    transition:all 300ms;
}

@media (max-width: 576px) { 

    .menu-open{
        left:0;
        width:100%;
    }
 }

 .collapse-menu-icons{
     color:white;
